p, h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  position: relative;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav {
  height: 100%;
}

.nav__list {
  flex-direction: row;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  text-align: center;
  height: 100%;
  display: flex;
}

.nav__item {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 100%;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.nav__item:hover {
  color: #00acdc;
}

.nav .is-active {
  color: #00acdc;
  margin-left: 0;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

* {
  box-sizing: border-box;
}

.main {
  grid-template-columns: auto;
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px 72px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: auto auto;
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: auto auto auto;
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: auto auto auto auto;
  }
}

.card {
  cursor: pointer;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__top {
  text-align: center;
  height: 134px;
}

.card__image {
  width: 160px;
  height: 100%;
}

.card__name {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s;
}

.card__name:hover {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price-block {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-word {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  margin-top: 16px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars:nth-child(-n+1), .stars--2 .stars:nth-child(-n+2), .stars--3 .stars:nth-child(-n+3), .stars--4 .stars:nth-child(-n+4), .stars--5 .stars:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.button {
  color: #fff;
  text-align: center;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s;
}

.button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

